@import url("https://fonts.googleapis.com/css2?family=Nanum+Gothic&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-size: 10px;
}
body {
  background-color: lightskyblue;
  font-family: "Nanum Gothic", sans-serif;
  font-size: 1.8rem;
  color: #6c8dd5;
}
h1 {
  text-align: center;
  margin-bottom: 50px;
}
h2 {
  margin-top: 50px;
  text-align: center;
}
textarea {
  display: block;
  width: 700px;
  height: 100px;
  margin: 0 auto;
}
.wrapper {
  max-width: 700px;
  margin: 0 auto;
  padding: 20px;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px,
    rgba(0, 0, 0, 0.22) 0px 15px 12px;
  background-color: #6c8dd5;
}
.container {
  display: flex;
  flex-direction: column;
  row-gap: 10px;
}
.row {
  display: flex;
  justify-content: space-between;

  .key {
    display: flex;
    flex-basis: 40px;
    justify-content: center;
    align-items: center;
    background-color: #2a4580;

    height: 40px;
    box-shadow: rgba(0, 0, 0, 0.17) 0px -23px 25px 0px inset,
      rgba(0, 0, 0, 0.15) 0px -36px 30px 0px inset,
      rgba(0, 0, 0, 0.1) 0px -79px 40px 0px inset,
      rgba(0, 0, 0, 0.06) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px,
      rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px,
      rgba(0, 0, 0, 0.09) 0px 32px 16px;
    transition: all 0.3s;

    &:hover {
      background-color: #1142aa;
      color: #fff;
      cursor: pointer;
      border-radius: 10px;
    }
    span {
      width: 100%;
      text-align: center;
      line-height: 40px;
    }
  }
}
.caseDown,
.caseUp,
.caps,
.shiftCaps {
  display: block;
  width: 40px;
  height: 40px;
  padding: 0 10px;
}
.hidden {
  display: none;
}
.key.pressed {
  background-color: #080e5e;
  color: #fff;
  border-radius: 10px;
}

.row .Space {
  width: 100px;
}
